div.tfw-view-InputColor {
    display: inline flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

div.tfw-view-InputColor.wide {
    display: flex;
}

div.tfw-view-InputColor .tfw-view-Input input {
    font-family: monospace;
    min-width: 8rem;
}

div.tfw-view-InputColor > div.flex {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
}

div.tfw-view-InputColor > div.flex > * {
    flex: 0 0 auto;
}

div.tfw-view-InputColor > div.flex > *:first-child {
    flex: 1 1 auto;
}




div.tfw-view-InputColor>div.flex div.tfw-view-Slider.slider {
    flex: 1 1 auto;
    min-width: 6rem;
}

div.tfw-view-InputColor>div.summary>div.tfw-view-Touchable {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
}

div.tfw-view-InputColor>div.summary>div.tfw-view-Touchable>div {
    max-height: 2rem;
    min-height: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 0 0 auto;
    line-height: 2rem;
    font-family: monospace;
    font-weight: bolder;
    min-width: 8rem;
    max-width: 8rem;
}

div.tfw-view-InputColor>div.summary>div.tfw-view-Touchable>div>div {
    margin: 0 .5rem;
}

div.tfw-view-InputColor>div.summary>div.tfw-view-Touchable>input.invalid {
    font-weight: normal;
    text-decoration: line-through;
}

div.tfw-view-InputColor > .slider {
    min-width: 4rem;
}