.tfw-view-Input {
    position: relative;
    display: inline-flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    margin: 0 .5rem;
    padding-bottom: 1rem;
    transition: all .2s;
}

.tfw-view-Input.wide {
    display: flex;
    width: calc(100% - 1rem);
    box-sizing: border-box;
    margin: 0 .5rem;
}

.tfw-view-Input > div.labels {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.tfw-view-Input > div.labels > *:first-child {
    flex: 1 1 auto;
}

.tfw-view-Input > div.labels > *:nth-child(2) {
    flex: 2 1 auto;
    text-align: right;
}

.tfw-view-Input > div.input {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.tfw-view-Input > div.input > * {
    flex: 0 0 auto;
}

.tfw-view-Input > div.input > input {
    flex: 1 1 auto;
    width: 100%;
    font-size: inherit;
    font-family: inherit;
    border: none;
    padding: 0 .25rem;
    line-height: 2rem;
    transition: all .2s;
    color: var(--thm-color-on-3);
}

.tfw-view-Input.invalid > div.input > input {
    padding-right: 2rem;
}

.tfw-view-Input > div.input > input:focus {
    outline: 2px solid var(--thm-color-SL);
    z-index: 1;
    background: var(--thm-color-3);
}

.tfw-view-Input.invalid input {
    position: relative;
    text-decoration-style: wavy;
    text-decoration-color: red;
    text-decoration-color: var(--thm-color-E);
}

.tfw-view-Input > div.input > div.icon {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 2rem;
    position: absolute;
    right: 0;
    z-index: 2;
}

.tfw-view-Input > div.input > input[disabled] {
    opacity: .5;
    font-style: italic;
}