div.tfw-layout-Tabstrip {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}

div.tfw-layout-Tabstrip>header {
    flex: 0 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
    margin: 0;
    z-index: 1;
}

div.tfw-layout-Tabstrip>header>div.void {
    flex: 1 1 auto;
}

div.tfw-layout-Tabstrip>header>button{
    font-weight: bold;
    flex: 0 0 auto;
    cursor: pointer;
    border: none;
    font-variant: small-caps;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;
    min-height: 2em;
    max-height: 2em;
    height: 2em;
    margin: 0;
    padding: 0 1em;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    border-radius: .25rem .25rem 0 0;
}

div.tfw-layout-Tabstrip>header>button>div {
    margin: 0;
    padding: 0;
    opacity: 0.5;
}
div.tfw-layout-Tabstrip>header>button.selected>div {
    opacity: 1;
}

div.tfw-layout-Tabstrip>header>button:active,
div.tfw-layout-Tabstrip>header>button:hover {
    filter: brightness(1.2);
}

div.tfw-layout-Tabstrip>div.body {
    flex: 1 1 auto;
}

