.tfw-view-Error {
    margin: 0;
    width: 100%;
    line-height: 1rem;
    font-size: 80%;
    font-weight: bold;
    padding: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left;
    transition: opacity .3s;
    opacity: 1;
}

.tfw-view-Error.hide {
    opacity: 0;
}
