@keyframes tfw-view-Icon-rotation {
    from {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

.tfw-view-Icon {
    display: inline-block;
    transition: all .3s;
    padding: 0;
    transform: scale(1);
    opacity: 1;
}

.tfw-view-Icon.active {
    cursor: pointer;
}

.tfw-view-Icon.animate {
    animation: tfw-view-Icon-rotation 1.1s linear infinite;
}

.tfw-view-Icon.disabled {
    cursor: default;
    opacity: .5;
}

.tfw-view-Icon .thm-svg-fill0 {
    fill: currentColor;
}

.tfw-view-Icon .thm-svg-stroke0 {
    stroke: currentColor;
}