.tfw-layout-Stack {
    position: relative;
    overflow: hidden;
}

.tfw-layout-Stack.fullscreen {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.tfw-layout-Stack.fullscreen>*,
.tfw-layout-Stack.animating>* {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.tfw-layout-Stack>div {
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.tfw-layout-Stack.scrollable>div {
    overflow-y: auto;
}

.tfw-layout-Stack.animating {
    pointer-events: none;
    overflow: hidden;
}

.tfw-layout-Stack.animating>* {
    animation-duration: var(--duration, .3s);
    animation-fill-mode: forwards;
}


/** opacity **/
@keyframes tfw-layout-Stack-ANIM-opacity-push-first {}

@keyframes tfw-layout-Stack-ANIM-opacity-push-last {
    from {
        opacity: 0
    }

    to {
        opacity: 1
    }
}

@keyframes tfw-layout-Stack-ANIM-opacity-pop-first {}

@keyframes tfw-layout-Stack-ANIM-opacity-pop-last {
    from {
        opacity: 1
    }

    to {
        opacity: 0
    }
}

.tfw-layout-Stack.anim-opacity.push>*:first-child {
    animation-name: tfw-layout-Stack-ANIM-opacity-push-first
}

.tfw-layout-Stack.anim-opacity.push>*:last-child {
    animation-name: tfw-layout-Stack-ANIM-opacity-push-last
}

.tfw-layout-Stack.anim-opacity.pop>*:first-child {
    animation-name: tfw-layout-Stack-ANIM-opacity-pop-first
}

.tfw-layout-Stack.anim-opacity.pop>*:last-child {
    animation-name: tfw-layout-Stack-ANIM-opacity-pop-last
}

