.tfw-view-List {
    position: relative;
    overflow: auto;
    padding: 0;
    margin: 0;
    min-width: 280px;
    max-height: 100vh;
    /* To prevent a bug in Google Chrome */
    overflow-anchor: none;
}

.tfw-view-List.refreshing>div.body {
    filter: blur(8px);
    transition: none;
    pointer-events: none;
}

.tfw-view-List>div {
    margin: 0;
    display: block;
    width: 100%;
    min-width: 100%;
}

.tfw-view-List>div.body {
    filter: blur(0);
    overflow: hidden;
}

.tfw-view-List>div.body>div.item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    margin: 0;
    padding: 0;
}

.tfw-view-List>div.body>div.item>*,
.tfw-view-List>div.body>div.item>div {
    width: 100%;
}

.tfw-view-List>div.space {
    font-size: 0;
    content: '.';
    background-repeat: repeat-y;
    background-position: 0 0;
    background-size: 263px 36px;
    background-image: url("./item-blur.webp");
}

.tfw-view-List>div.screen {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    z-index: 1;
    text-align: center;
    overflow: hidden;
}

.tfw-view-List>div.screen>button {
    transform: translateY(-110%);
    transition: transform .2s;
}

.tfw-view-List.animate-refresh>div.screen>button {
    transform: translateY(100%);
    transition: transform .3s;
}