.tfw-view-Expand {
    display: block;
    padding: 0 16px;
}

.tfw-view-Expand .head {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 32px;
    font-family: Roboto, josefin-sans, sans-serif;
    font-size: 14px;
    font-weight: bold;
    font-variant: small-caps;
    font-variant: common-ligatures small-caps;
    cursor: pointer;
}

.tfw-view-Expand .head>div.icons {
    position: relative;
    width: 24px;
    height: 24px;
    padding: 0;
    margin: 0 8px 0 0;
}

.tfw-view-Expand .head>div.icons>* {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    transition: all .3s;
}

.tfw-view-Expand .head>div.icons>*:first-child {
    transform: rotate(0);
    opacity: 1;
}

.tfw-view-Expand .head>div.icons>*:last-child {
    transform: rotate(-90deg);
    opacity: 0;
}

.tfw-view-Expand[aria-expanded="true"] .head>div.icons>*:first-child {
    transform: rotate(90deg);
    opacity: 0;
}

.tfw-view-Expand[aria-expanded="true"] .head>div.icons>*:last-child {
    transform: rotate(0);
    opacity: 1;
}


.tfw-view-Expand>div.body {
    visibility: hidden;
    overflow: hidden;
    padding: 0 0 0 16px;
    margin: 0;
    transition: none;
    opacity: 0;
    max-height: 0;
    transition: none;
}

.tfw-view-Expand[aria-expanded="true"]>div.body {
    visibility: visible;
    overflow: unset;
    opacity: 1;
    transition: all .5s;
    max-height: max-content;
}