*,
*:after,
*:before {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

body,
html {
    height: 100%;
    margin: 0;
    padding: 0;
}

body.thm-font-size-small {
    font-size: 16px;
}

body.thm-font-size-medium {
    font-size: 19px;
}

body.thm-font-size-large {
    font-size: 24px;
}

hr {
    display: block;
    width: 100%;
    border: none;
    height: 1px;
    background-image: linear-gradient(to right, transparent, rgba(0, 0, 0, .5) 50%, transparent);
    background-image: linear-gradient(to right, transparent, var(--thm-color-on-1-7) 50%, transparent);
}

button {
    font-size: inherit;
    font-family: inherit;
}

/* Try to remove focus decorations */
a::-moz-focus-inner,
button::-moz-focus-inner {
    outline: 0;
    border: 0;
}
a:focus,
a:active,
button:focus,
button:active {
    text-decoration: underline;
    text-decoration-style: dotted;
    border: 2px solid var(--thm-black3);
    outline: 0;
}
a, a:visited {
    color: var(--thm-color-link);
}

/* Headings */
h1,h2,h3 {
  font-weight: bold;
  border: 0.0625em solid #000f;
  border: 0.0625em solid var(--thm-color-on-1-7);
  border-image: linear-gradient(to right, var(--thm-color-on-1-9), transparent) 0 0 1 0;
}
h1 { font-size: 120%; margin-left: 0rem; }
h2 { font-size: 115%; margin-left: 0.5rem; }
h3 { font-size: 110%; margin-left: 1rem; }

