.tfw-view-Button {
    display: inline-flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    margin: .5rem;
    vertical-align: inherit;
}

.tfw-view-Button.wide {
    display: flex;
}

.tfw-view-Button.small {
    margin: .25rem;
}

.tfw-view-Button.error {
    margin-bottom: 1.5rem;
}

.tfw-view-Button.small.error {
    margin-bottom: 1.25rem;
}

.tfw-view-Button>button {
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 2.25rem;
    border: 2px solid transparent;
    transition: all .2s;
    vertical-align: middle;
    min-height: 2.25rem;
    padding: 0;
    text-decoration: none;
    border-radius: 0.13rem;
    text-shadow: none;
    cursor: pointer;
    font-family: Roboto, josefin-sans, sans-serif;
    font-size: 0.88rem;
    font-weight: bold;
    width: 7rem;
}

.tfw-view-Button.reverse>button>* {
    pointer-events: none;
}

.tfw-view-Button.small>button {
    min-width: auto;
}


.tfw-view-Button.floating>button {
    justify-content: center;
    border-radius: 50%;
    min-width: 3.5rem;
    width: 3.5rem;
    height: 3.5rem;
}

.tfw-view-Button.floating.small>button {
    width: 2.5rem;
    min-width: 2.5rem;
    height: 2.5rem;
    max-height: 2.5rem;
}

.tfw-view-Button.floating.wide>button {
    border-radius: 2000vw;
}

.tfw-view-Button.hide>button,
.tfw-view-Button.hide.wide>button {
    display: none;
}

.tfw-view-Button.flat>button {
    background-color: transparent;
}

.tfw-view-Button>button>div {
    flex: 1 1 auto;
    order: 1;
}

.tfw-view-Button>button>svg {
    flex: 0 0 auto;
    order: 0;
}

.tfw-view-Button.reverse>button svg {
    order: 2;
}

.tfw-view-Button>button>div.text.hide {
    display: none;
}

.tfw-view-Button>button>div.text {
    flex-grow: 0;
    order: 1;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    text-align: center;
    width: 100%;
    /* text-transform: uppercase; */
    font-variant: small-caps;
    margin: 0 0.5rem;
}

.tfw-view-Button.inverted>button>div.text {
    order: -1;
    text-align: right;
}

.tfw-view-Button.no-text>button>div.text {
    display: none;
}

.tfw-view-Button>button>.icon {
    order: 0;
    flex-grow: 0;
    margin: 0 0.5rem 0 0.25rem;
    padding: 0;
}

.tfw-view-Button>button[disabled],
.tfw-view-Button:hover>button[disabled] {
    pointer-events: none;
    text-decoration: none;
    cursor: default;
    filter: saturate(0);
    text-shadow: none;
    box-shadow: none;
}

.tfw-view-Button>button[disabled]>*,
.tfw-view-Button:hover>button[disabled]>* {
    opacity: .5;
}

.tfw-view-Button::-moz-focus-inner>button {
    outline: 0;
    border: 0;
}

.tfw-view-Button:focus>button,
.tfw-view-Button:active>button {
    text-decoration: underline;
    text-decoration-style: dotted;
    border: 2px solid var(--thm-color-on-3-3);
    outline: 0;
}

/* On small screen, responsive mode hides the text when there is an icon. */
@media (max-width: 480px) {
    .tfw-view-Button.responsive>button>div.text {
        display: none;
    }

    .tfw-view-Button.responsive>button {
        max-width: 2rem;
        min-width: 2rem;
        width: 2rem;
        height: 2rem;
        margin: 0;
        border-radius: 100%;
        overflow: hidden;
    }
}

/* Small version */
.tfw-view-Button.small>button {
    min-height: 1.5rem;
    max-height: 1.5rem;
    height: 1.5rem;
    margin: 0;
    font-size: 0.625rem;
    font-weight: normal;
}

.tfw-view-Button.small>button>div.text {
    text-align: left;
}

.tfw-view-Button.smal>button>.tfw-view-icon {
    margin: 0 0.25rem 0 0.125rem;
}